.cart-dropdown {
    position: absolute;
    width: 240px;
    height: 340px;
    display: flex;
    flex-direction: column;
    padding: 20px;
    border: 1px solid black;
    background-color: white;
    top: 115px;
    right: 6%;
    z-index: 5;

    .cart-items {
        height: 240px;
        display: flex;
        flex-direction: column;
        overflow-y: auto;

    }

    .empty-message {
        font-size: 18px;
        margin: 50px auto;
    }

    button {
        margin-top: auto;
    }


}